.timer-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  background: radial-gradient(circle at center, #2a2a2a 0%, #1a1a1a 100%);
}

.timer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.motivational-text {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-color);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1rem;
  animation: fadeInOut 0.5s ease-in-out;
  text-shadow: 0 0 20px rgba(0, 255, 136, 0.3);
  transition: all 0.3s ease;
}

.timer-circle {
  position: relative;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(145deg, #1f1f1f, #1a1a1a);
  animation: float 6s ease-in-out infinite;
  transition: all 0.3s ease;
  padding: 20px;
  box-shadow: 0 0 40px rgba(0, 255, 136, 0.2);
}

.timer-circle::before {
  content: '';
  position: absolute;
  width: 92%;
  height: 92%;
  border-radius: 50%;
  background: linear-gradient(145deg, #1a1a1a, #1f1f1f);
  z-index: 0;
  animation: rotateShadow 10s linear infinite;
}

@keyframes rotateShadow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.timer-circle::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(circle at center, transparent 60%, rgba(0, 255, 136, 0.1) 100%);
  z-index: 3;
  pointer-events: none;
  animation: pulse 2s ease-in-out infinite;
  filter: blur(8px);
}

.progress-ring {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.progress-ring circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke: var(--primary-color);
  stroke-width: 8;
  stroke-linecap: round;
  transform: rotate(-90deg);
  transform-origin: center;
  transition: stroke-dashoffset 0.3s ease;
}

.progress-ring circle.background {
  stroke: rgba(0, 255, 136, 0.1);
}

.progress-ring circle.progress {
  stroke-dasharray: 880;
  stroke-dashoffset: 880;
  filter: drop-shadow(0 0 5px var(--primary-color));
}

.timer-display {
  position: relative;
  z-index: 2;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
}

.time-unit-group {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.time-unit {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  position: relative;
}

.time-digit {
  font-size: 5.5rem;
  font-weight: 700;
  color: var(--primary-color);
  line-height: 1;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  text-shadow: 0 0 20px rgba(0, 255, 136, 0.3);
  width: 60px;
  display: inline-block;
  text-align: center;
  transition: all 0.3s ease;
}

.time-separator {
  font-size: 5.5rem;
  font-weight: 700;
  color: var(--primary-color);
  line-height: 1;
  opacity: 0.7;
  margin: 0 0.2rem;
  animation: separatorGlow 1.5s ease-in-out infinite;
  text-shadow: 0 0 20px rgba(0, 255, 136, 0.3);
}

@keyframes separatorGlow {
  0%, 100% {
    opacity: 0.7;
    text-shadow: 0 0 20px rgba(0, 255, 136, 0.3);
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    text-shadow: 0 0 30px rgba(0, 255, 136, 0.5);
    transform: scale(0.95);
  }
}

.digit-adjust {
  background: transparent;
  border: none;
  color: var(--primary-color);
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0.5;
  text-shadow: 0 0 10px var(--primary-color);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.3s ease-in-out;
}

.controls {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
  perspective: 1000px;
}

.control-button {
  padding: 1.2rem 3rem;
  font-size: 1.2rem;
  font-weight: 700;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  min-width: 180px;
  color: #1a1a1a;
  text-transform: uppercase;
  letter-spacing: 3px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(45deg, var(--primary-color), #00ff88);
  background-size: 200% auto;
  animation: gradientMove 3s ease infinite;
  box-shadow: 0 8px 25px rgba(0, 255, 136, 0.3);
}

@keyframes gradientMove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.control-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent,
    rgba(255, 255, 255, 0.4),
    transparent
  );
  transition: 0.6s;
}

.control-button:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 255, 136, 0.4);
  letter-spacing: 5px;
}

.control-button:hover::before {
  left: 100%;
}

.control-button:active {
  transform: translateY(0) scale(0.95);
  box-shadow: 0 5px 15px rgba(0, 255, 136, 0.3);
  letter-spacing: 3px;
}

@media (max-width: 480px) {
  .timer-circle {
    width: 300px;
    height: 300px;
  }

  .time-digit {
    font-size: 4rem;
    width: 45px;
  }

  .time-separator {
    font-size: 4rem;
  }

  .digit-adjust {
    font-size: 1rem;
    width: 30px;
    height: 30px;
  }

  .digit-adjust.up {
    top: -35px;
  }

  .digit-adjust.down {
    bottom: -35px;
  }

  .motivational-text {
    font-size: 1.8rem;
  }

  .controls {
    gap: 1rem;
  }

  .control-button {
    min-width: 140px;
    padding: 1rem 2rem;
    font-size: 1rem;
    letter-spacing: 2px;
  }
}

@media (max-width: 480px) {
  .control-button {
    min-width: 140px;
    padding: 1rem 2rem;
    font-size: 1rem;
    letter-spacing: 2px;
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.7;
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.05);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}